import PrefLoader from "./Components/PrefLoader";
import Authentication from "./Components/Authentication";
import Localization from "./Components/Localization";
import Boot from "./States/Boot";
import Connecting from "./States/Connecting";
import MainMenu from "./States/MainMenu";
import Options from "./States/Options";
import Play from "./States/Play";

export default class Backgamon extends Phaser.Game {
    constructor() {
        super(1280, 720, Phaser.AUTO, "", null, false, true);
        this.muted = false;

        this.l10ns = {};
        this.languages = ["en", "ar"];
        this.strings = [];

        this.prefs = new PrefLoader();
        this.muted = this.prefs.get("muted", false);
        //this.name = this.prefs["name"];

        this.state.add("Boot", Boot, true);
        this.state.add("MainMenu", MainMenu);
        this.state.add("Options", Options);
        this.state.add("Play", Play);
        this.state.add("Connecting", Connecting);
    }

    toggleFullscreen() {
        if (this.scale.isFullScreen) this.scale.stopFullScreen();
        else this.scale.startFullScreen(false);
    }
}

Backgamon.guidesX = [177, 259, 341, 424, 506, 588, 692, 774, 865, 938, 1020, 1102];
Backgamon.guidesY = [685, 30];
Backgamon.columnWidth = 81;
Backgamon.maxColumnHeight = 265;
Backgamon.pieceDefaultScale = Backgamon.columnWidth / 238;
Backgamon.pieceHeight = Backgamon.pieceDefaultScale * 238;
