import TextButton from "../Components/TextButton";

export default class Options extends Phaser.State {
    constructor(game_) {
        super();
        this.game = game_;
    }

    preload() {
    }

    create() {
        this.game.stage.backgroundColor = "#947c66";

        new TextButton(this.game, "button", this.game.world.centerX, 437, "Back", this.game.l10n.styles.local.button, function () { this.game.state.start("MainMenu"); }, this);
    }

    render() {
    }
}
