export default class Localization {
    constructor(name, game) {
        this.game = game;
        this.code = name;
        game.load.json("lang-" + name, "assets/Languages/" + name + ".json");
    }

    load() {
        var lFile = this.game.cache.getJSON("lang-" + this.code);
        this.name = lFile.name;
        this.font = lFile.font;
        this.strings = lFile.strings;
        this.styles = { "default": {
                "title": { font: "bold 60pt Nunito", fill: "rgb(231, 231, 231)", align: "center" },
                "button": { font: "bold 37pt Nunito", fill: "rgb(231, 231, 231)", align: "center" },
                "text": { font: "bold 30pt Nunito", fill: "rgb(231, 231, 231)", align: "center" },
            },
            "local": {
                "title": { font: "bold 60pt " + this.font, fill: "rgb(231, 231, 231)", align: "center" },
                "button": { font: "bold 37pt " + this.font, fill: "rgb(231, 231, 231)", align: "center" },
                "text": { font: "bold 30pt " + this.font, fill: "rgb(231, 231, 231)", align: "center" },
            } };
    }
}
