import io from "socket.io-client_";
import Authentication from "../Components/Authentication";
import TextButton from "../Components/TextButton";

export default class Connecting extends Phaser.State {
    constructor(game) {
        super();
        this.game = game;
    }

    create() {
		this.group = new Phaser.Group(this.game);
		this.game.stage.backgroundColor = "#947c66";
		var text = new Phaser.Text(this.game, this.game.world.centerX, this.game.world.centerY, this.game.strings["Contacting Server"], this.game.l10n.styles.local.text);
		this.group.add(text);
		text.anchor.set(0.5);
		text.setShadow(2, 2, 'rgba(0,0,0,0.5)', 0);

		this.connected = false;
		var self = this;

		var socket = io("//localhost:8088");
		socket.on("welcome", function () {
			self.succeeded(socket);
		});

		setTimeout(function () {
			if (!self.connected) {
				socket.disconnect();
				self.failed();
			}
		}, 2000);
    }

    failed() {
		this.group.removeAll();
		var text = new Phaser.Text(this.game, this.game.world.centerX, this.game.world.centerY-50, this.game.strings["Contacting Failed"], this.game.l10n.styles.local.text);
		this.group.add(text);
		text.anchor.set(0.5);
		text.setShadow(2, 2, 'rgba(0,0,0,0.5)', 0);
		text.wordWrap = true;
		text.wordWrapWidth = 1200;

		var OKButton = new TextButton(this.game, "button", this.game.world.centerX, this.game.world.centerY+100, this.game.strings["OK"], this.game.l10n.styles.local.button, function () {
			this.game.state.start("MainMenu");
		}, this, this.group);
    }

    succeeded(socket) {
        if (!this.connected) {
            this.connected = true;
            this.game.auth = new Authentication(this.game);
            this.game.state.start("MainMenu");
        }
        this.game.auth.setRemote(socket);
    }
}
