export default class TextButton {
    constructor(game, key, x, y, text, fontStyle, onClick, callbackContext, group) {
        this.onClick_ = onClick;
        if (group) this.button_ = group.add(new Phaser.Button(game, x, y, key, null, this, 1, 1, 0, 1));
        else this.button_ = game.add.button(x, y, key, null, this, 1, 1, 0, 1);

        this.button_.events.onInputOut.add(function() {
            this.text_.y = this.origY;
        }, this);
        this.button_.events.onInputDown.add(function() {
            this.text_.y = this.origY+6;
        }, this);
        this.button_.events.onInputUp.add(function(sprite, pointer, isOver) {
            this.text_.y = this.origY;
            game.add.audio("beep").play();
            if (isOver) this.onClick_.apply(callbackContext);
        }, this);

        this.button_.anchor.set(0.5);

        this.origY = y-15;
        if (group) this.text_ = group.add(new Phaser.Text(game, x, this.origY, text, fontStyle));
        else this.text_ = game.add.text(x, this.origY, text, fontStyle);
        this.text_.anchor.set(0.5);

        this.text_.setShadow(3, 3, 'rgba(0,0,0,0.5)', 0);
    }

    hide() {
        this.text_.visible = false;
        this.button_.visible = false;
        this.button_.inputEnabled = false;
    }

    show() {
        this.text_.visible = true;
        this.button_.visible = true;
        this.button_.inputEnabled = true;
        this.button_.input.useHandCursor = true;
    }
}
