import Backgamon from "../Backgamon";

export default class Piece extends Phaser.Sprite {
    constructor(game, color, x, y) {
        super(game, x, y, "piece", (color == "black") ? 0 : 1);
        game.add.existing(this);
        this.anchor.set(0.5);
        this.scale.set(Backgamon.pieceDefaultScale);
        this.color = color;
        this.events.onInputUp.add(function () {
            console.log("clicked");
        }, this);
    }

    grow() {
        var tween = this.game.add.tween(this.scale);
        tween.to({ "x": Backgamon.pieceDefaultScale * 1.1, "y": Backgamon.pieceDefaultScale * 1.1 }, 200);
        tween.start();
    }

    shrink() {
        var tween = this.game.add.tween(this.scale);
        tween.to({ "x": Backgamon.pieceDefaultScale, "y": Backgamon.pieceDefaultScale }, 200);
        tween.start();
    }
}

export class PieceGlow extends Phaser.Sprite {
    constructor(game, x, y, callback, callbackContext, group) {
        super(game, x, y, "piece-glow");
        if (group) group.add(this);
        else game.add.existing(this);
        this.anchor.set(0.5);
        this.scale.set(Backgamon.pieceDefaultScale);

        if (callback) this.events.onInputUp.add(function () {
            callback.apply(callbackContext);
        }, this);

        this.inputEnabled = true;
        this.input.useHandCursor = true;

		// Add a "bouncy" effect.
        this.scale.set(Backgamon.pieceDefaultScale * 1.1);
        var tween = this.game.add.tween(this.scale);
        tween.to({ "x": Backgamon.pieceDefaultScale * 1.15, "y": Backgamon.pieceDefaultScale * 1.15 }, 500, "Sine.easeInOut", true, 0, -1, true);
        tween.start();
    }
}
