import TextButton from "../Components/TextButton";

export default class MainMenu extends Phaser.State {
    constructor(game) {
        super();
        this.game = game;
        var self = this;
    }

    preload() {
        this.game.load.spritesheet("button", "assets/UI/button.png", 328, 134);
    }

    create() {
		this.game.stage.backgroundColor = "#947c66";

		var titleText = this.game.add.text(this.game.world.centerX, 50, this.game.strings["Backgamon"], this.game.l10n.styles.local.title);
		titleText.anchor.x = 0.5;
		titleText.setShadow(3, 5, 'rgba(0,0,0,0.5)', 0);

		var playButton = new TextButton(this.game, "button", this.game.world.centerX, 280, this.game.strings["Play"], this.game.l10n.styles.local.button, function () { this.game.state.start("Play"); }, this);
		var fullscreenButton = new TextButton(this.game, "button", this.game.world.centerX, 430, this.game.strings["Fullscreen"], this.game.l10n.styles.local.button, function () { this.game.toggleFullscreen(); }, this);
		var soundButton = new TextButton(this.game, "button", this.game.world.centerX, 580, this.game.strings["Sound"]+": "+(!this.game.muted? this.game.strings["ON"]: this.game.strings["OFF"]), this.game.l10n.styles.local.button, function () {
			this.game.sound.volume = this.game.muted? 1: 0;
			soundButton.text_.setText(this.game.strings["Sound"]+": "+(this.game.muted? this.game.strings["ON"]: this.game.strings["OFF"]));
			this.game.muted = !this.game.muted;
			this.game.prefs.set("muted", this.game.muted);
		}, this);
		// var musicButton = new TextButton(this.game, "button", this.game.world.centerX+170, 580, this.game.l10n["Music"]+": "+this.game.l10n["On"], this.game.buttonFontStyle, function () {  }, this);
		// musicButton.text_.scale.set(0.9);
		//new TextButton(this.game, "button", this.game.world.centerX, 591, "Quit", Backgamon.buttonFontStyle, function () { console.log("quit clicked"); });
    }

    render() {
		//this.game.debug.text(this.game.time.fps.toString() || "--", 2, 14, "#00ff00");
    }
}
