export default class PrefLoader {
    constructor() {
    }

    set(key, value) {
        localStorage[key] = JSON.stringify(value);
    }

    get(key, defaultValue) {
        var data = localStorage[key];
        if (data) return JSON.parse(data);
        else return (defaultValue === undefined)? PrefLoader.defaults[key] :defaultValue;
    }
}

PrefLoader.defaults = { "language": "ar", "sound": true };
