export default class Dice {
    constructor(game, x, y) {
        this.sprite = game.add.sprite(x, y, "dice", 0);
        this.sprite.visible = false;
        this.sprite.anchor.set(0.5);
        this.y = y;
        
        this.sprite.animations.add("roll", null, 10, true);
    }

    roll(direction, value, callback, callbackContext) {
        // direction is 1 or -1. 1 means the dice rolls from the bottom up. -1 mean top down.
        this.sprite.visible = true;
        this.sprite.y = 360 + 424 * direction;
        this.sprite.scale.set(1.5);

        // Do th rolling effect.
        // The rolling effect consists of three parts: the movement, rotations, sprite "animation", and scaling.
        // Scaling is done in 3 parts.
        var tweenPos = this.sprite.game.add.tween(this.sprite);
        tweenPos.to({ "y": this.y }, 1000);
        tweenPos.start();
        var tweenRot = this.sprite.game.add.tween(this.sprite);
        tweenRot.to({ "angle": (Math.floor(Math.random() * 2) ? -1 : 1) * 359 }, Math.random() * 300 + 500, null, false, 0, Infinity);
        tweenRot.start();
        var tweenScale = this.sprite.game.add.tween(this.sprite.scale);
        tweenScale.to({ "x": 1, "y": 1 }, 300, Phaser.Easing.Cubic.Out);
        tweenScale.onComplete.add(function () {
            var tweenScale2 = this.sprite.game.add.tween(this.sprite.scale);
            tweenScale2.to({ "x": 1.2, "y": 1.2 }, 300, Phaser.Easing.Cubic.InOut);
            tweenScale2.onComplete.add(function () {
                var tweenScale3 = this.sprite.game.add.tween(this.sprite.scale);
                tweenScale3.to({ "x": 1, "y": 1 }, 400, Phaser.Easing.Cubic.Out);
                tweenScale3.onComplete.add(function () {
                    this.sprite.animations.stop();
                    this.sprite.frame = this.value - 1;
                    tweenRot.stop();
                    if (callback)
                        callback.apply(callbackContext);
                }, this);
                tweenScale3.start();
            }, this);
            tweenScale2.start();
        }, this);
        tweenScale.start();

        this.sprite.animations.play("roll");

        this.value = value;
    }
}
