import TextButton from "../Components/TextButton";
import Column from "../Components/Column";
import Dice from "../Components/Dice";

export default class Play extends Phaser.State {
    constructor(game) {
        super();
        this.game = game;
        this.side = "black";
    }

    preload() {
        this.game.load.image("board", "assets/Game/board.png");
        this.game.load.spritesheet("piece", "assets/Game/piece.png", 238, 238, 2, 0, 24);
        this.game.load.image("piece-glow", "assets/Game/piece-glow.png");
        this.game.load.spritesheet("dice", "assets/Game/dice.png", 64, 64);
        this.game.load.audio("roll", "assets/Sounds/roll.mp3");
        this.game.load.audio("stack", "assets/Sounds/stack.wav");
		//this.game.load.shader("bacteria", "assets/Shaders/bacteria.frag");
    }

    create() {
		this.game.stage.backgroundColor = "#947c66";

		var board = this.game.add.image(this.game.world.centerX, this.game.world.centerY, "board");
		board.anchor.set(0.5);
		board.scale.set(720.0/561.0);

		this.columns = [];
		for (let i = 0; i < 24; i++) {
			this.columns.push(new Column(this.game, i, this.side));
		}
		this.columns[0].place(this.side, 15);
		this.columns[23].place(this.side=="black"? "white": "black", 15);

		this.dice1 = new Dice(this.game, 360, 360);
		this.dice2 = new Dice(this.game, 920, 360);

		this.rollButton = new TextButton(this.game, "button", this.game.world.centerX, this.game.world.centerY, this.game.strings["Roll"], this.game.l10n.styles.local.button, function () {
			this.rollDice(false);
			this.rollButton.hide();
		}, this);

		this.glowies = this.game.add.group();

		this.createPauseMenu();

		this.pauseButton = this.game.add.button(1200, 60, "controls", function () {
			this.togglePauseMenu();
		}, this);
		this.pauseButton.freezeFrames = true;
		this.pauseButton.frame = 5;
		this.pauseButton.anchor.set(0.5);
		this.pauseButton.scale.set(0.45);

	    //this.glowFilter = new Phaser.Filter(this.game, null, this.game.cache.getShader("bacteria"));
    	//(<any>this.glowFilter).addToWorld(0, 0, 1280, 720);
		//this.game.add.existing(this.glowFilter);
    }

    showMoves(d1, d2) {
        for (let i = 0; i < 24; i++) {
            if (!(d1 === 0 && d2 === 0) && this.columns[i].group.length && this.columns[i].group.getTop().color == this.side) this.columns[i].showPossibleMove(true, function () {
                    this.selected = this.columns[i];
                    this.setMoves(d1, d2, this.columns[i].location);
            }, this, this.glowies);
            else this.columns[i].hidePossibleMove();
        }
    }

    setMoves(d1, d2, location) {
        for (let i = 0; i < 24; i++) {
            if ((d1 && i == location + d1) || (d2 && i == location + d2)) this.columns[i].showPossibleMove(false, function () {
                    this.selected.moveTo(this.columns[i]);
                    setTimeout(function (self) {
                        self.game.add.audio("stack").play();
                    }, 200, this);
                    this.selected = null;
                    if (d2 == 0)
                        this.nextTurn();
                    else {
                        var dx = d1;
                        if (i == location + d1)
                            dx = d2;
                        this.showMoves(dx, 0);
                    }
            }, this, this.glowies);
            else this.columns[i].hidePossibleMove();
        }
    }

    nextTurn() {
        this.rollButton.show();
    }

    rollDice(otherPlayer) {
        var d1 = Math.floor(Math.random() * 5) + 1;
        var d2 = Math.floor(Math.random() * 5) + 1;
        this.dice1.roll(otherPlayer ? -1 : 1, d1);
        var self = this;
        this.dice2.roll(otherPlayer ? -1 : 1, d2, function () {
            this.showMoves(d1, d2);
        }, this);
        setTimeout(function (self) {
            self.game.add.audio("roll").play();
        }, 800, this);
    }

    createPauseMenu() {
        this.pauseMenu = this.game.add.group();
        this.pauseMenu.visible = false;

        var width = 1280;
        var height = 720;
        var bmd = this.game.add.bitmapData(width, height);

        bmd.ctx.beginPath();
        bmd.ctx.rect(0, 0, width, height);
        bmd.ctx.fillStyle = '#000000';
        bmd.ctx.fill();

        var overlay = new Phaser.Sprite(this.game, 0, 0, bmd);
        overlay.alpha = 0.5;
        overlay.inputEnabled = true;
        this.pauseMenu.add(overlay);

        var resumeButton = new TextButton(this.game, "button", this.game.world.centerX, this.game.world.centerY - 225, this.game.strings["Resume"], this.game.l10n.styles.local.button, function () {
            this.togglePauseMenu();
        }, this, this.pauseMenu);

        var soundButton = new TextButton(this.game, "button", this.game.world.centerX, this.game.world.centerY - 75, this.game.strings["Sound"] + ": " + (!this.game.muted ? this.game.strings["ON"] : this.game.strings["OFF"]), this.game.l10n.styles.local.button, function () {
            this.game.sound.volume = this.game.muted ? 1 : 0;
            soundButton.text_.setText(this.game.strings["Sound"] + ": " + (this.game.muted ? this.game.strings["ON"] : this.game.strings["OFF"]));
            this.game.muted = !this.game.muted;
            this.game.prefs.set("muted", this.game.muted);
        }, this, this.pauseMenu);

		/*var musicButton = new TextButton(this.game, "button", this.game.world.centerX, this.game.world.centerY-75, "Music: ON", this.game.buttonFontStyle, function () {
		}, this, this.pauseMenu);*/

        var fullscreenButton = new TextButton(this.game, "button", this.game.world.centerX, this.game.world.centerY + 75, this.game.strings["Fullscreen"], this.game.l10n.styles.local.button, function () {
            this.game.toggleFullscreen();
        }, this, this.pauseMenu);

        var quitButton = new TextButton(this.game, "button", this.game.world.centerX, this.game.world.centerY + 225, this.game.strings["Quit"], this.game.l10n.styles.local.button, function () {
            this.endGame();
        }, this, this.pauseMenu);
    }

    togglePauseMenu() {
        this.pauseMenu.visible = !this.pauseMenu.visible;
        if (this.pauseMenu.visible) {
            this.pauseButton.frame = 6;
        } else {
            this.pauseButton.frame = 5;
        }
    }

    endGame() {
        this.game.state.start("MainMenu");
    }

    render() {
		//this.game.debug.text(this.game.input.x+" "+this.game.input.y, 2, 14, "#00ff00");
		//1192 45
    }

    update() {
	    //this.glowFilter.update();
    }
}
