import Backgamon from "../Backgamon";
import Piece, {PieceGlow} from"./Piece";

export default class Column {
    constructor(game, location, side) {
        this.game = game;
        this.location = location;
        this.group = this.game.add.group();

        if (side == "black") {
            this.dir = 1;
            this.x = this.location % 12;
            if (this.location < 12) {
                this.dir = -1;
                this.x = 11 - this.x;
            }
            this.x = Backgamon.guidesX[this.x];
            this.y = Backgamon.guidesY[Math.floor(this.location / 12)];
        } else {
            this.dir = -1;
            this.x = this.location % 12;
            if (this.location >= 12) {
                this.dir = 1;
                this.x = 11 - this.x;
            }
            this.x = Backgamon.guidesX[this.x];
            this.y = Backgamon.guidesY[Math.floor(this.location / 12)];
        }
        this.label = this.game.add.text(this.x, this.y, "", this.game.l10n.styles.default.button);
        this.label.visible = false;
        this.label.anchor.set(0.5);
        this.label.setShadow(3, 3, 'rgba(0,0,0,0.5)', 0);
    }

    getPieceHeight(length) {
        return Math.min(Backgamon.maxColumnHeight / length, Backgamon.pieceHeight);
    }

    reorderY(length) {
        if (!length) length = this.group.length;
        else if (length < 1) length = this.group.length + length;
        var pieceHeight = this.getPieceHeight(length);

        for (let i = 0; i < this.group.length; i++) {
            var piece = (this.group.getAt(i));
            var tween = this.game.add.tween(piece);
            tween.to({ "y": this.y + (pieceHeight * i * this.dir) }, 200);
            tween.start();
        }

        if (length > 4) {
            this.label.visible = true;
            this.label.y = this.y + (pieceHeight * (length - 1) * this.dir);
            this.label.text = length.toString();
        } else this.label.visible = false;
    }

    place(color, length) {
        if (!length) length = 1;
        var pieceHeight = this.getPieceHeight(this.group.length + length);

        for (let i = 0; i < length; i++) {
            var piece = new Piece(this.game, color, this.x, this.y + (pieceHeight * i * this.dir));
            piece.alpha = 0;
            var tween = this.game.add.tween(piece);
            tween.to({ "alpha": 1 }, 200);
            tween.start();
            this.group.add(piece);
        }
        this.reorderY();
    }

    take(length) {
        if (!length) length = 1;

        if (this.group.length - length < 0) {
            console.error("Taking more than is available on column", this.location);
            return;
        }

        for (let i = 0; i < length; i++) {
            this.group.removeBetween(this.group.length - length, this.group.length - 1);
        }

        this.reorderY();
    }

    moveTo(other) {
        if (!this.group.length) {
            console.error("Moving more than is available on column", this.location);
            return;
        }

        this.hidePossibleMove();
        other.hidePossibleMove();

        var piece = this.group.getTop();
        other.reorderY(other.group.length + 1);
        this.group.remove(piece);
        other.group.add(piece);

        var tween = this.game.add.tween(piece);
        tween.to({ "x": other.x, "y": other.y + (other.getPieceHeight(other.group.length) * other.dir * (other.group.length - 1)) }, 200);
        tween.start();
        this.reorderY();
    }

    showPossibleMove(inside, callback, callbackContext, group) {
        if (this.glow) this.hidePossibleMove();
        this.glow = new PieceGlow(this.game, this.x, this.y + (this.getPieceHeight(this.group.length) * (inside ? this.group.length - 1 : this.group.length) * this.dir), callback, callbackContext, group);
        if (inside) this.group.getTop().grow();
    }

    hidePossibleMove() {
        if (this.group.length) this.group.getTop().shrink();

        if (this.glow) {
            this.glow.destroy();
            this.glow = null;
        }
    }
}
