export default class Authentication {
    constructor(game) {
        this.game = game;
        this.name = (Math.random() * 1000).toString();
        this.password = "";
    }

    setRemote(socket) {
        this.socket = socket;
        socket.emit("auth", this.name, this.password);
        socket.on("auth.acc", this.authAccepted);
    }

    authAccepted(sessionID) {
        this.sid = sessionID;
    }
}
