import Localization from "../Components/Localization";

export default class Boot extends Phaser.State {
    constructor(game) {
        super();
        this.game = game;
    }

    preload() {
        for (let i = 0; i < this.game.languages.length; i++) {
            this.game.l10ns[this.game.languages[i]] = new Localization(this.game.languages[i], this.game);
        }
        this.game.load.spritesheet("button", "assets/UI/button.png", 328, 134);
        this.game.load.spritesheet("controls", "assets/UI/controls.png", 244, 244);
        this.game.load.audio("beep", "assets/Sounds/beep.wav");
    }

    fileComplete(progress) {
        this.progress.text = progress + "%";
    }

    create() {
        for (let key in this.game.l10ns) {
            this.game.l10ns[key].load();
        }

        this.game.l10n = this.game.l10ns[this.game.prefs.get("language")];
        this.game.strings = this.game.l10n.strings;

        //this.game.scale.scaleMode = Phaser.ScaleManager.SHOW_ALL;
        this.game.scale.fullScreenScaleMode = Phaser.ScaleManager.SHOW_ALL;
        this.progress = this.game.add.text(this.game.world.centerX, this.game.world.centerY - 30, "0%", { fill: 'white' });
        this.progress.anchor.setTo(0.5, 0.5);

		//var progressBar = this.game.add.sprite(this.game.world.centerX, this.game.world.centerY, "progress-bar");
		//progressBar.anchor.setTo(0.5, 0.5);
		//this.game.load.setPreloadSprite(progressBar);

        this.game.load.onFileComplete.add(this.fileComplete, this);
        this.game.state.start("Connecting");
    }

    render() {
    }
}
